import { MainHeader } from "./UI Components/MainHeader";
import { MainFooter } from "./UI Components/MainFooter/MainFooter";
import Button from "./UI Components/Button";
import Sheet from "./UI Components/Cards and Sections/Sheet/Sheet";
import Card from "./UI Components/Cards and Sections/Card/Card";
import { SKILLS } from "./assets/resumeDetails";
import ReactLogo from "./assets/React-logo-icon.svg";
import Star from "./UI Components/Star";
import LinkedInLogo from "./assets/linkedin-logo.png";
import webDevelopementLeft from "./assets/web-development-left.gif";
import webDevelopementRight from "./assets/web-development-right.gif";

import "./app.scss";

const App = () => (
  <>
    <MainHeader title={"Siva Kumar"} hasLogo={true}>
      <Button text="My Skills" isTextOnlyButton={true} />
      <Button text="Download Resume" isTextOnlyButton={true} />
      <Button text="Contact Me" isPrimaryButton={true} />
    </MainHeader>
    <Sheet className="profile-section">
      <div className="webDevelopementLeft">
        <img src={webDevelopementLeft} alt="gif" />
      </div>
      <div className="profile-description">
        <h4>Hello, I am a</h4>
        <h1>Full Stack Developer</h1>
        <p>
          Passionate developer having hands on experience in various
          technologies like ReactJS, Redux, Java and NodeJS. Experience in
          developing client-server applications using ReactJS, Redux, HTML,
          JavaScript, CSS3. And also have knowledge on Micro Services
          Architecture, Micro Front End Architecture, knowledge of relational
          databases MS SQL Server, Oracle. A good team player and fast learner
          who adapts quickly with changing technology.
        </p>
      </div>
      <div className="webDevelopementRight">
        <img src={webDevelopementRight} height={350} width={350} alt="gif" />
      </div>
    </Sheet>
    <Sheet headerText="Skills" className="skills">
      {SKILLS.map((skill) => (
        <Card headerIcon={ReactLogo} headerText={skill.name}>
          <div className="expertise">
            {skill.expertise}
            <Star value={skill.expertise} />
          </div>
          <div className="experience">{skill.experience}</div>
        </Card>
      ))}
    </Sheet>
    <MainFooter copyRightMessage={"Â© copyright Siva Kumar Burle"}>
      <a href="https://www.linkedin.com/in/siva-kumar-burle-09b2b8a4">
        <img src={LinkedInLogo} alt="LinkedIn Logo" />
      </a>
    </MainFooter>
  </>
);

export default App;
